@import "./../../assets//styles/utils/functions";

.card-team-wrapper {
  color: #2d2d2d;

  .decor {
    width: vw(2);
    height: vw(504) !important;
  }

  .header-banner {
    width: 100vw;
    height: vw(272);
    position: relative;
    overflow: hidden;
    border-bottom: vw(2) solid #231e14;

    @include mobile {
      height: vw(206);
      border-top: vw(2) solid #7f6e41;
    }

    .box-container {
      inset: 0;
      display: flex;
      margin: 0 auto;
      position: absolute;
      max-width: vw(1202);
      align-items: flex-end;

      @include mobile {
        padding-left: vw(20);
      }
    }

    figure {
      height: vw(272);
    }

    .header-title {
      z-index: 9;
      font-size: vw(63);
      line-height: vw(71);
      margin-bottom: vw(41);
      letter-spacing: -0.005em;
      font-weight: var(--font-extra);

      @include mobile {
        font-size: vw(48);
        line-height: vw(48);
        margin-bottom: vw(29);
      }
    }
  }

  .teams-wrapper {
    width: vw(1202);
    margin: 0 auto;
    padding: vw(103) 0 vw(160);

    @include mobile {
      padding-top: vw(67);
      width: 100%;
    }

    .team {
      margin-bottom: vw(152);

      @include mobile {
        margin-bottom: vw(88);
      }

      &:last-child {
        margin-bottom: 0;
      }
      .team-header {
        gap: vw(58);
        display: flex;
        font-size: vw(45);
        line-height: vw(71);
        align-items: center;
        letter-spacing: -0.01em;
        margin-bottom: vw(66);
        justify-content: space-between;
        font-weight: var(--font-semi-bold);

        @include mobile {
          gap: vw(26);
          font-size: vw(34);
          line-height: vw(44);
          padding-left: vw(20);
          margin-bottom: vw(39);
        }

        &:after {
          content: "";
          height: vw(2);
          flex: 1 auto;
          display: block;
          background-color: #7f6e41;
          transform: translate3d(0, vw(6), 0);
        }
      }

      .members {
        display: grid;
        gap: vw(105);
        grid-template-columns: repeat(3, 1fr);

        @include mobile {
          gap: vw(36);
          padding: 0 vw(20);
          grid-template-columns: 1fr;
        }

        .member {
          width: 100%;

          @include mobile {
            width: vw(327);
            margin: 0 auto;
          }

          figure {
            width: 100%;
            height: vw(313);
            img {
              width: 100%;
              height: 100%;
            }
          }

          .detail {
            height: vw(189);
            color: #cecece;
            overflow: hidden;
            position: relative;
            background-color: #231e14;
            border-top: vw(2) solid #937d4d;

            @include mobile {
              height: vw(167);
            }

            &::before,
            &::after {
              top: 0;
              left: 0;
              content: "";
              width: vw(18);
              height: vw(88);
              position: absolute;
              border-right: vw(2) solid #7a7a7a;

              @include mobile {
                width: vw(16);
              }
            }

            &::before {
              background-color: #937d4d;

              @include mobile {
                height: vw(67);
              }
            }

            &::after {
              top: vw(87);
              height: vw(99);

              @include mobile {
                top: vw(67);
              }
            }

            .name {
              color: #fff;
              display: flex;
              height: vw(88);
              font-size: vw(29);
              font-family: vw(42);
              padding-left: vw(38);
              align-items: flex-end;
              padding-bottom: vw(7);
              font-weight: var(--font-extra);
              border-bottom: vw(2) solid #7a7a7a;

              @include mobile {
                height: vw(67);
                font-size: vw(28);
                line-height: vw(42);
                padding-left: vw(36);
                padding-bottom: vw(2);
                letter-spacing: -0.005em;
              }
            }

            a {
              gap: vw(9);
              color: #bc9f64;
              font-size: vw(14);
              line-height: vw(18);
              align-items: center;
              margin-left: vw(2);
              letter-spacing: 0.008em;
              display: inline-flex;
              border-bottom: 1px solid #bc9f64;

              @include mobile {
                font-size: vw(19);
                letter-spacing: 0;
                line-height: vw(23);
              }

              svg {
                width: vw(8);
                height: vw(8);
                flex: 0 0 vw(8);

                @include mobile {
                  width: vw(9);
                  height: vw(9);
                  flex: 0 0 vw(9);
                }
              }
            }

            .footer {
              padding-top: vw(12);
              padding-left: vw(38);

              @include mobile {
                padding-left: vw(36);
              }

              .position {
                font-size: vw(20);
                font-style: italic;
                line-height: vw(24);
                margin-bottom: vw(20);

                @include mobile {
                  font-size: vw(19);
                  line-height: vw(22);
                }
              }
            }
          }
        }
      }
    }
  }
}
